footer{
  background: var(--color-primary);
  margin: 2rem 0 0 0;
  height: 200px;
}

footer a{
  color:var(--color-bg-variant);
}

.footer__logo{
  padding: 2rem 0 0 1rem;
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
}

.footer__socials{
  display:flex;
  flex-wrap:wrap;
  justify-content:center;
  gap:1rem;
  padding-top:1.5rem;
}

.footer__copyright{
  text-align: center;
  margin-top: 3.7rem;
  color:var(--color-white);
}
