.header__container{
  margin:1.5rem 2.5rem 2.5rem 4.5rem;
  padding-top: 10rem;

  height:100%;
  text-align:center;
  display:grid;
  grid-template-columns: 1fr 1fr;
}

.cta{
   text-align:left;
}

.me img{
  position: relative;
  left:3.9%;
  top:4%;
  height:92%;
  width:92%;
  border-radius: 50%;
  transition: var(--transition);
}

.me img:hover{
  transform:scale(1.07);
  transition:1s ease-out;
}

.me img:hover:after{
  transform:scale(1.07);
  transition:1s ease-in;
}

.me{
  position: relative;
  left:30%;
  width:21rem;
  height:21rem;
  box-shadow: 0 1px 0px 0px var(--color-white);
  border-radius:50%;
}

.intro__me h1{
  padding-top: 1rem;
  position: relative;
  bottom:2%;
}
.intro__me h5{
  padding-bottom: 0;
  margin:0 1.5rem 0 0;
  position: relative;
  top:7%;
}

.cta{
  text-align: center;
  padding-top: 2rem;
  gap: 0.8rem;
  position: relative;
  right:1%;
}

.svg__container{
  position: relative;
  left:30%;
  bottom:5%;
}



.cta .a{
  border-radius: 1rem;
  margin:auto;
  padding: 4rem 0 1.5rem 1.5rem;
  text-align:center;

}

.cta a{
  color:#fff;
}

.scroll{
  position: relative;
  right:1.4%;
  margin:auto;
  background: var(--color-bg-variant);
  color:var(--color-white);
  border-radius: 1rem;
  width:7rem;
  height:4rem;
}

.scroll a{
  position: relative;
  top:20%;
  color:var(--color-white);
}

.scroll a:hover{
  color:var(--color-bg);
}




.header__socials{
  text-align: center;
  padding-top: 1rem;
  position: relative;
  right:3%;
}

.arrow {
  position: relative;
  top: 10px;
  display: center;
}


.header__socials a{

  padding: 1.5rem 0 1.5rem 1.5rem;
  text-align: center;
}

