.about__container{
  display:grid;
  grid-template-columns:35% 50%;
  gap:15%;
}

.about__me{
  width:100%;
  height:97%;
  aspect-ratio:1/1;
  border-radius:2rem;
  background:linear-gradient(45deg,transparent,var(--color-peach),transparent);
  display:grid;
  place-items:center;

}

.about__me-image{
  height:120%;
  border-radius:2rem;
  overflow:hidden;
  transform:rotate(10deg);
  transition: var(--transition);
}
.about__me-image:hover{
  transform:rotate(0);
}

.about__cards{
  display:grid;
  grid-template-columns:repeat(3,1fr);
  gap:1.5rem;
}

.about__card{
  background:var(--color-bg-variant);
  border:1px solid transparent;
  border-radius:1rem;
  padding:0.5rem;
  text-align:center;
  transition: var(--transition);
}

.about__card:hover{
  background:transparent;
  border-color:var(--color-primary-variant);
  cursor:default;
}



.about__icon{
  color:var(--color-peach);
  font-size:1.4rem;
  margin-bottom:1rem;
}

h5{
  font-size:0.8rem;
  color:var(--color-white) !important;
}



.about__card small{
  font-size:0.7rem;
  color:var(--color-light);
}

.about__content p{
  margin:2rem 0 2.6rem;
  color:var(--color-light);
}


 /**************MEDIUM**********/
@media screen and (max-width:1024px){
   .about__container{
     grid-template-columns: 1fr;
     gap:-5;


   }
   .about__me{
     width:50%;
     margin:2rem auto 4rem;

   }

   .about__content p{
     margin:1.5rem 0 1.5rem;

   }


}

/*******768px******/

@media screen and (max-width:375px){
  .about__me{
    width:45%;
    margin:0 auto 3rem;
    margin-top: 2rem;
  }
  .about__content p{
    font-size:2rem;
  }
}



/***********SMALL***********/
@media screen and (max-width:600px){
  .about__me{
    width:65%;
    margin:0 auto 3rem;
    margin-top: 2rem;
  }
  .about__cards{
    grid-template-columns:1fr;
    gap:1rem;
    margin-top: 0.2rem;
  }

  .about__card{
    padding:0.5rem;

  }
  .about__content{
    text-align:center;
  }
  .about__content p{
    margin:1.5rem 0;
  }
}
